.main-nav {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 1;
    margin:0 .5rem 3rem;

    .nav-link {
        position: relative;
        flex: 1 0 0;
        display: flex;
        justify-content: center;
        align-items: center;
        max-width: 450px;
        height: 450px;
        font-size: 3vw;
        background-color: $main-dark;
        color: $main-bright;
        text-decoration: none;
        transition: .5s;

        &:nth-child(2) {
            margin: 0 .5rem;
        }
    }
}

.shrinked-main-nav {
    display: flex;
    flex-flow: wrap;
    height: 9rem;
    transition: .5s;

    .shrinked-nav {
        position: absolute;
        height: 3rem;
        font-size: 2rem;
        transition: .5s;
        width: calc(50% - 1px);
        max-width: 50%;
        min-width: fit-content;
        &:hover {
            background-color: $main-dark-050;
        }
        &:nth-child(3) {
            right: 0;
        }
        
        &:nth-child(2) {
            margin: 0 ;
        }
    }

    .right-nav {
        right: 0;
    }

    .active-nav {
        position: absolute;
        top: 5rem;
            height: 5rem;
            font-size: 3rem;
            transition: .5s;
            background-color: $main-dark-050;
            backdrop-filter: blur(3px);
            // color: $main-dark;
            width: 100%;
            max-width: 100%;
            &:hover {
                background-color: $main-dark-050;
                backdrop-filter: blur(3px);
                cursor: context-menu;
            }
    }
}

.home-link {
    text-decoration: none;
    &:not(.shrinked-nav) {
        cursor: context-menu;
    }
    
    span {
        position: relative;
        font-family: $fat;
    }
}
.home-link.shrinked-nav:focus {
    border: 1px solid white;
}

.home-link.shrinked-nav:hover {
    .simon-s{animation: blink-simon-s .5s;}
    .simon-i{animation: blink-simon-i .5s;}
    .simon-m{animation: blink-simon-m .5s;}
    .simon-o{animation: blink-simon-o .5s;}
    .simon-n{animation: blink-simon-n .5s;}
    .schoetz-s{animation: blink-schoetz-s .5s;}
    .schoetz-c{animation: blink-schoetz-c .5s;}
    .schoetz-h{animation: blink-schoetz-h .5s;}
    .schoetz-ö{animation: blink-schoetz-oe .5s;}
    .schoetz-t{animation: blink-schoetz-t .5s;}
    .schoetz-z{animation: blink-schoetz-z .5s;}
}

@keyframes blink-simon-s {
    0% {opacity: 1;}
    1% {opacity: .3;}
    10% {opacity: .3;}
    11% {opacity: 1;}
    100% {opacity: 1;}
}
